    position: fixed;
    bottom: 0;
    width: 100%;
}
#footer ul{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    line-height: 1.8rem;
    height: 1.8rem;
    color: #fff;
    font-size: 1.4rem;
    padding: 1rem 0;
    /*justify-content: space-around;*/
}
#footer ul li{
    text-align: center;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    -webkit-flex:1;
}
.router-link-active{
    color: red
}



