
.main-img {
  content: '';
  display: block;
  overflow: hidden;
  clear: both;
}
.main-img ul {
    width: 50%;
    float: left;
}
.main-img ul li {
      border: solid 1px #cccccc;
      box-shadow: 0 0 5px #CCCCCC;
      border-radius: 5px;
      padding: 5%;
      box-sizing: border-box;
      margin: 1rem 0;
}
.main-img ul li img {
        width: 100%;
}
/*懒加载插件*/
img[lazy=loading] {
  background-image: url(../../static/img/load.gif);
  background-repeat: no-repeat;
  background-position: center;
}        
 
        



